import axios from 'axios';

// 在 js 中导入 element-ui 的 Message 弹出框
import {Message} from 'element-ui';
import da from "element-ui/src/locale/lang/da";
import router from '../router/index'


/**
 * 用弹窗的方式，处理全局的异常
 *  success 里面的不一定登陆成功，会有各种
 *  error 里面一定失败
 *
 *  success.data.status  相当于下面的 status
 *  {
 *	"status": 500,
 *	"msg": "尚未登陆，请登录!",
 *	"obj": null
 *  }
 */
axios.interceptors.response.use(
    success => {
        // 如果 success.status==200 success.data.status==500 的话，可能是业务上的错误
        if (success.status && success.status === 200 && success.data.status === 500) {
            Message.error({message: success.data.msg})
            return;
        }
        // 登陆成功时的提醒
        if(success.data.msg) {
            Message.success({message: success.data.msg})
        }
        // 返回到请求调用的地方
        return success.data;
    },
    error => {
        if (error.response.status === 504 || error.response.status === 404) {
            Message.error({message: '服务器被吃了( ╯□╰ )'})
        } else if (error.response.status === 403) {
            Message.error({message: '权限不足，请联系管理员！'})
        } else if (error.response.status === 401) {
            Message.error({message: '尚未登陆，请登录！'})
            router.replace('/');  // 跳转到登录页
        } else {
            // 显示服务端返回的错误信息
            if (error.response.data.msg) {
                Message.error({message: error.response.data.msg})
            } else {
                // 如果服务端也没返回错误
                Message.error({message: '未知错误'})
            }
        }
        return;
    }
)

// 封装请求  post 请求的封装  base 是为以后统一加前缀
let base = '';
// 登陆的请求 Security 只支持 key value   Security
export const postKeyValueRequest = (url, params) => {
    return axios({
        method: 'post',
        url: `${base}${url}`,
        data: params,
        // 把 params（json） 转成 key value
        transformRequest: [function (data) {
            let ret = '';
            for (let i in data) {
                // 拼接成 key=value&  形式
                ret+=encodeURIComponent(i) + '=' + encodeURIComponent(data[i]) + '&'
            }
            return ret;
        }],
        headers: {
            'Content_Type':'application/x-www-form-urlencoded'
        }
    })
}

// 传递 json 的 post
export const postRequest = (url, params) => {
    return axios({
        method: 'post',
        url: `${base}${url}`,
        data: params
    })
}

export const putRequest = (url, params) => {
    return axios({
        method: 'put',
        url: `${base}${url}`,
        data: params
    })
}

export const getRequest = (url, params) => {
    return axios({
        method: 'get',
        url: `${base}${url}`,
        data: params
    })
}

export const deleteRequest = (url, params) => {
    return axios({
        method: 'delete',
        url: `${base}${url}`,
        data: params
    })
}