import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import {initMenu} from "@/utils/menus";
import 'font-awesome/css/font-awesome.min.css'  // 导入 font-awesome 图标库

import {postKeyValueRequest} from "@/utils/api";
import {postRequest} from "@/utils/api";
import {getRequest} from "@/utils/api";
import {putRequest} from "@/utils/api";
import {deleteRequest} from "@/utils/api";

/**
 * 把 api.js 里面的请求方式 做成插件方便调用
 * 调用时用 this.XXX
 */
Vue.prototype.postKeyValueRequest = postKeyValueRequest;
Vue.prototype.postRequest = postRequest;
Vue.prototype.getRequest = getRequest;
Vue.prototype.putRequest = putRequest;
Vue.prototype.deleteRequest = deleteRequest;

Vue.config.productionTip = false

Vue.use(ElementUI, {size: 'small'}); // 统一给全局组件配置

/**
 * 前置导航守卫   相当于  过滤器
 *  to : 从哪儿来
 *  from : 去哪里
 */
router.beforeEach((to, from, next) => {
    // 如果去登陆页  直接通过
    if (to.path === '/') {
        next();
    } else {
        // 判断有没有登陆，登陆了可以继续往下走，没登陆就要登陆
        if (window.sessionStorage.getItem("user")) {
            // 初始化页面
            initMenu(router, store);
            next();
        } else {
            // 登陆完之后跳转的路径， 因为不能每次都跳转到首页
            next('/?redirect=' + to.path)   // 去登录页
        }
    }
})

new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#app')
