import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/Login'
import Home from '../views/Home'
import FriendChat from "../views/chat/FriendChat";
import HrInfo from "@/views/HrInfo";


Vue.use(VueRouter)

const routes = [
    {
        name: 'Login',
        path: '/',
        component: Login,
        hidden: true  // 只是个标记 在 home 循环显示时为了去除
    },
    {
        name: 'Home',
        path: '/home',
        component: Home,
        hidden: true,
        meta: {
            roles: ['admin', 'user']
        },
        children: [
            {
                name: '在线聊天',
                path: '/chat',
                component: FriendChat,
                hidden: true  // 只是个标记 在 home 循环显示时为了去除
            },
            {
                name: '个人中心',
                path: '/hrinfo',
                component: HrInfo,
                hidden: true  // 只是个标记 在 home 循环显示时为了去除
            },
        ]
    },
]

const router = new VueRouter({
  routes
})

export default router
