<template>
    <div>
        <el-form
                ref="loginForm"
                :rules="rules"

                v-loading="loading"
                element-loading-text="正在登陆..."
                element-loading-spinner="el-icon-loading"
                element-loading-background="rgba(0, 0, 0, 0.8)"

                :model="loginForm"
                class="loginContainer">
            <h3 class="loginTitle">系统登陆</h3>
            <!-- prop 是 username 的校验方式来校验此项 -->
            <el-form-item prop="username">
                <el-input type="text" v-model="loginForm.username" auto-complete="off"
                          size="normal"
                          placeholder="请输入用户名" @keydown.enter.native="submitLogin"></el-input>
            </el-form-item>
            <el-form-item prop="password">
                <el-input type="password" v-model="loginForm.password" auto-complete="off"
                          size="normal"
                          placeholder="请输入密码"></el-input>
            </el-form-item>
            <el-form-item prop="code">
                <el-input type="text" v-model="loginForm.code" auto-complete="off"
                          size="normal"
                          placeholder="点击图片更换验证码" @keydown.enter.native="submitLogin" style="width: 250"></el-input>
                <img :src="vcUrl" @click="updateVerifyCode" alt="">
            </el-form-item>
            <el-checkbox size="normal" class="loginRemember" v-model="checked">记住我</el-checkbox>
            <el-button size="normal" type="primary" style="width: 100%" @click="submitLogin">登陆</el-button>
        </el-form>
    </div>
</template>

<script>
    // 在 main.js 俩做成了插件
    // import {postKeyValueRequest} from "@/utils/api";

    export default {
        name: "Login",
        data() {
            return {
                // 验证码 url 地址
                vcUrl: '/verifyCode?time=' + new Date(),
                // 验证码
                code: '',
                // 进度条
                loading: false,
                // 验证规则  验证用户名和密码
                rules: {
                    username: [{required: true, message: '请输入用户名', trigger: 'blur'}],
                    password: [{required: true, message: '请输入密码', trigger: 'blur'}],
                    code: [{required: true, message: '请输入验证码', trigger: 'blur'}]
                },
                // 数据对象， 定义默认的用户名密码
                loginForm: {
                    username: '',
                    password: ''
                },
                checked: false
            }
        },
        methods: {
            updateVerifyCode() {
                // 点击图片刷新验证码
                this.vcUrl = '/verifyCode?time=' + new Date();
            },
            submitLogin() {
                this.$refs.loginForm.validate((valid) => {
                    if (valid) {
                        this.loading = true; // 显示进度条
                        // 做成插件之后不用 import 直接 this.xxxx 使用
                        this.postKeyValueRequest('/doLogin', this.loginForm).then(resp => {
                            this.loading = false  // 不管成功还是失败，进度条必须关闭
                            // 只需要处理成功的即可， 失败的已经处理过了
                            if (resp) {
                                this.$store.commit('INIT_CURRENTHR', resp.obj)
                                // 把前端收到的用户信息保存在 storage  只能存字符串
                                window.sessionStorage.setItem("user", JSON.stringify(resp.obj))
                                // 这个 path 是用户登陆之前的 path， 方便用户登陆完之后在跳到来的页面
                                let path = this.$route.query.redirect;  // redirect 是 main.js 传来的
                                // 登陆成功 跳转页面 $router 是在 main.js 就传进来的
                                // this.$router.push()  // 还能退回到 login 页
                                this.$router.replace((path === '/' || path === undefined) ? '/home' : path)  // 不能后退到 login 页
                            } else {
                                // 登陆失败就自动刷新一次
                                this.vcUrl = '/verifyCode?time=' + new Date();
                            }
                        })
                    } else {
                        this.$message.error('请输入所有字段')
                        return false;
                    }
                })
            }
        }
    }
</script>

<style>
    .loginContainer {
        border-radius: 15px;
        background-clip: padding-box;
        margin: 180px auto;
        width: 350px;
        padding: 15px 35px 15px 35px;
        background: #ffffff;
        border: 1px solid #eaeaea;
        box-shadow: 0 0 25px #cac6c6;
    }

    .loginTitle {
        margin: 15px auto 20px auto;
        text-align: center;
        color: #505458;
    }

    .loginRemember {
        text-align: left;
        margin: 0 0 15px 0;
    }

    .el-form-item__content {
        display: flex;
        align-items: center;
    }
</style>