<template>
    <div>
        <el-container>
            <el-header class="homeHeader">
                <div class="title">微人事</div>
                <div>
                    <el-button icon="el-icon-bell" type="text" style="margin-right: 10px;color: #000000;" size="normal" @click="goChat"></el-button>
                    <el-dropdown class="userInfo" @command="commandHandler">
                  <span class="el-dropdown-link">
                    {{user.name}}
                      <i>
                        <img :src='user.userFace' alt="">
                      </i>
                  </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item command="userinfo">个人中心</el-dropdown-item>
                            <el-dropdown-item command="setting">设置</el-dropdown-item>
                            <el-dropdown-item command="logout" divided>注销登录</el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </div>
            </el-header>
            <el-container>
                <el-aside width="200px">
                    <!-- 加上 router 能自动跳转， 会自动找到 index 属性，！ 不用 menuClick 方法了 -->
                    <!-- <el-menu @select="menuClick" router>-->
                    <el-menu router unique-opened>
                        <!-- 循环 router/index.js 里的 routes， 显示 hidden 属性不为 false 的 router -->
                        <!-- 改了，循环的优势下面 computed 里面的 routes -->
                        <el-submenu :index="index + ' '" v-for="(item,index) in routers" v-if="!item.hidden">
                            <template slot="title">
                                <i style="color: #5395e5; margin-right: 5px" :class="item.iconCls"></i>
                                <span>{{item.name}}</span>
                            </template>
                            <el-menu-item v-for="(child,indexj) in item.children" :index="child.path" :key="indexj">
                                {{child.name}}
                            </el-menu-item>
                        </el-submenu>
                    </el-menu>
                </el-aside>
                <el-main>
                    <!-- 面包屑 -->
                    <el-breadcrumb separator-class="el-icon-arrow-right"
                                   v-if="this.$router.currentRoute.path !== '/home'">
                        <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
                        <el-breadcrumb-item>{{this.$router.currentRoute.name}}</el-breadcrumb-item>
                    </el-breadcrumb>
                    <div class="homeWelcome" v-if="this.$router.currentRoute.path === '/home'">
                        欢迎来到微人事！
                    </div>
                    <router-view class="homeRouterView"/>
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>

<script>
    export default {
        name: "Home",
        data() {
            return {
                // 获取 storage 里的 用户信息， 在转成 Json
                // user: JSON.parse(window.sessionStorage.getItem('user')),
            }
        },
        computed: {
            routers() {
                return this.$store.state.routes;
            },
            user() {
                return this.$store.state.currentHr
            }
        },
        methods: {
            goChat() {
                this.$router.push('/chat');
            },
            commandHandler(cmd) {
                if (cmd === 'logout') {
                    this.$confirm('此操作将注销登录, 是否继续?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        // 点击确定 就访问 /logout 注销登录
                        this.getRequest('/logout');
                        // 登陆的数据情况
                        window.sessionStorage.removeItem('user');
                        // 回到登录页
                        this.$router.replace('/');
                        // 退出登陆后把 storage 里的数据清空，不然另一个人登陆还是这个和界面
                        this.$store.commit('initRoutes', [])
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '已取消操作'
                        });
                    });
                }else if (cmd === "userinfo") {
                    this.$router.push('/hrinfo')
                }
            },
            // menuClick(index) {
            //     // index 就是 <el-menu-item index="/test1"></el-menu-item> 中的 index
            //     this.$router.push(index)
            // }
        }
    }
</script>

<style>
    .homeRouterView {
        margin-top: 10px;
    }

    .homeWelcome {
        text-align: center;
        font-size: 35px;
        font-family: 华文行楷;
        color: #409eff;
        padding-top: 50px;
    }

    .homeHeader {
        background-color: #409eff;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 15px;
        box-sizing: border-box;
    }

    .homeHeader .title {
        font-size: 30px;
        font-family: 华文行楷;
        color: #f1f6fa;
    }

    .homeHeader .userInfo {
        cursor: pointer;
    }

    .el-dropdown-link img {
        width: 36px;
        height: 36px;
        border-radius: 24px;
        margin-left: 8px;
    }

    .el-dropdown-link {
        display: flex;
        align-items: center;
    }

</style>
